/* Radio Box */
.choice {
  display: block;
  position: relative;
}

.choice input[type=number],
.choice input[type=text] {
  height: 25px;
  width: 50px;
  text-align: right;
  border: 1px solid #ccc;
  border-radius: 6px;
  line-height: 21px;
  padding: 0 10px;
  -moz-appearance: textfield;
}

.choice input[type=number]::-webkit-outer-spin-button, .choice input[type=number]::-webkit-inner-spin-button,
.choice input[type=text]::-webkit-outer-spin-button,
.choice input[type=text]::-webkit-inner-spin-button {
  -ms-progress-appearance: unset !important;
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  margin: 0;
}

.choice input[type=radio] {
  position: absolute;
  visibility: hidden;
}

.choice input[type=radio]:checked ~ .check {
  border: 2px solid #4a7fb3;
}

.choice input[type=radio]:checked ~ .check::before {
  background: #4a7fb3;
}

.choice input[type=radio]:checked ~ label {
  color: #4a7fb3;
  font-weight: bold;
}

.choice label {
  -webkit-transition: all 0.25s linear;
  cursor: pointer;
  display: block;
  height: 25px;
  line-height: 25px;
  padding-left: 22px;
  position: relative;
  z-index: 9;
  color: #999999;
}

.choice .check {
  display: block;
  position: absolute;
  border: 2px solid #999999;
  border-radius: 100%;
  height: 15px;
  width: 15px;
  top: 5px;
  left: 0;
  z-index: 5;
  transition: border .25s linear;
  -webkit-transition: border .25s linear;
}

.choice .check::before {
  display: block;
  position: absolute;
  content: '';
  border-radius: 100%;
  height: 7px;
  width: 7px;
  top: 2px;
  left: 2px;
  margin: auto;
  transition: background 0.25s linear;
  -webkit-transition: background 0.25s linear;
}

.choice:hover .check {
  border: 2px solid #b3b3b3;
}

.box {
  margin-top: 20px;
  font-size: 1.25rem;
  /* 20 */
  background-color: #ffffff;
  position: relative;
  padding: 40px 5px;
}

.box.has-advanced-upload {
  border: 2px dashed #999999;
  border-radius: 6px;
  transition: outline-offset .15s ease-in-out, background-color .15s linear;
}

.box.is-dragover {
  border-color: #999999;
  background-color: #cccccc;
}

.box__dragndrop,
.box__icon {
  display: none;
}

.box.has-advanced-upload .box__dragndrop {
  display: inline;
}

.box.has-advanced-upload .box__icon {
  width: 100%;
  height: 40px;
  fill: #999999;
  display: block;
  margin-bottom: 20px;
}

.box.is-uploading .box__input, .box.is-success .box__input, .box.is-error .box__input {
  visibility: hidden;
}

.box__uploading, .box__success, .box__error {
  display: none;
}

.box.is-uploading .box__uploading, .box.is-success .box__success, .box.is-error .box__error {
  display: block;
  position: absolute;
  top: 50%;
  right: 0;
  left: 0;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.box__uploading {
  text-align: center;
  font-style: italic;
}

.box__success {
  text-align: center;
  -webkit-animation: appear-from-inside .25s ease-in-out;
  animation: appear-from-inside .25s ease-in-out;
}

@-webkit-keyframes appear-from-inside {
  from {
    -webkit-transform: translateY(-50%) scale(0);
  }
  75% {
    -webkit-transform: translateY(-50%) scale(1.1);
  }
  to {
    -webkit-transform: translateY(-50%) scale(1);
  }
}

@keyframes appear-from-inside {
  from {
    transform: translateY(-50%) scale(0);
  }
  75% {
    transform: translateY(-50%) scale(1.1);
  }
  to {
    transform: translateY(-50%) scale(1);
  }
}

.box__restart {
  font-weight: 700;
}

.box__restart:focus, .box__restart:hover {
  color: #b3b3b3;
}

.box .box__input .box__file {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

.box .box__input .box__file + label {
  color: #999999;
  width: 100%;
  text-align: center;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
  display: inline-block;
  overflow: hidden;
  font-size: 18px;
}

.box .box__input .box__file + label .box__dragndrop {
  font-size: 12px;
}

.box .box__input .box__file + label:hover strong {
  color: #b3b3b3;
}

.box .box__input .box__file:focus + label strong, .box .box__input .box__file.has-focus + label strong {
  color: #b3b3b3;
}

.box .box__input .box__file:focus + label, .box .box__input .box__file.has-focus + label {
  outline: 1px dotted #000;
  outline: -webkit-focus-ring-color auto 5px;
}

.box .box__input .box__file + label * {
  /* pointer-events: none; */
  /* in case of FastClick lib use */
}

.box .box__input .box__button {
  font-weight: 700;
  color: #e5edf1;
  background-color: #b3b3b3;
  display: none;
  padding: 8px 16px;
  margin: 40px auto 0;
}

.box .box__input .box__button:hover, .box .box__input .box__button:focus {
  background-color: #0f3c4b;
}

